import utils from './utils'

const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
}

const colors = ['#2185C5', '#7ECEFD', '#FFF6E5', '#FF7F66']

// Event Listeners
addEventListener('mousemove', (event) => {
  mouse.x = event.clientX
  mouse.y = event.clientY
})

addEventListener('resize', () => {
  canvas.width = innerWidth
  canvas.height = innerHeight

  init()
})

function randomIntFromRange(min,max) {
  return Math.floor(Math.random() * (max-min +1) + min);
}

function randomColor(colors) {
  return colors[Math.floor(Math.random() * (colors.lenght) )];
}

function getDistance(x1,y1,x2,y2) {
  let xDistance = x2 - x1;
  let yDistance = y2 - y1;

  // Pitagoras
  return Math.sqrt(Math.pow(xDistance,2) + Math.pow(yDistance,2)); 

}
// Objects
class Circulo {
  constructor(x, y, radius, color) {
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.closePath()
  }

  update() {
    this.draw()
  }
}

// Implementation
let circulo1;
let circulo2;


function init() {
  circulo1 = new Circulo(300,300,100,colors[0]);
  circulo2 = new Circulo(undefined,undefined,30,colors[1]);

}

// Animation Loop
function animate() {
  requestAnimationFrame(animate)
  c.clearRect(0, 0, canvas.width, canvas.height)
  circulo1.update();
  circulo2.x = mouse.x; 
  circulo2.y = mouse.y; 
  circulo2.update();

  if (getDistance(circulo1.x,circulo1.y,circulo2.x,circulo2.y) < circulo1.radius + circulo2.radius) {
    circulo1.color = 'yellow';
  } else {
    circulo1.color = colors[0];
  }
  //c.fillText(circulo1.update(), mouse.x, mouse.y)
  // objects.forEach(object => {
  //  object.update()
  // })
}

init()
animate()
